.hw3__wrapper {
    background: aquamarine;
    max-width: 700px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}
.hw3__title {
    margin: 0;
    font-size: 30px;
}


.addGreetings__wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 300px;
    padding-left: 30px;
}

.error {
    border: 1px solid red;
}

.error__message {
    font-size: 20px;
    color: red;
}

.proper__input {
    border: 1px solid black;
}

.addGreetings {
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 10px;
}

.addGreetings input {
    padding: 13px 20px;
}

.addGreetings button {
    align-items: center;
    appearance: none;
    background-color: #fff;
    border-radius: 24px;
    border-style: none;
    box-shadow: rgba(0, 0, 0, .2) 0 3px 5px -1px, rgba(0, 0, 0, .14) 0 6px 10px 0, rgba(0, 0, 0, .12) 0 1px 18px 0;
    box-sizing: border-box;
    color: #3c4043;
    cursor: pointer;
    display: inline-flex;
    fill: currentcolor;
    font-family: "Google Sans", Roboto, Arial, sans-serif;
    font-size: 20px;
    font-weight: 500;
    height: 43px;
    justify-content: center;
    line-height: normal;
    padding: 2px 24px;
    text-align: center;
    text-transform: none;
    transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1), opacity 15ms linear 30ms, transform 270ms cubic-bezier(0, 0, .2, 1) 0ms;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;

    will-change: transform, opacity;
    z-index: 0;
}

.addGreetings button:hover {
    background: #F6F9FE;
    color: #174ea6;
}

.addGreetings button:active {
    box-shadow: 0 4px 4px 0 rgb(60 64 67 / 30%), 0 8px 12px 6px rgb(60 64 67 / 15%);
    outline: none;
}

.addGreetings button:focus {
    outline: none;
    border: 2px solid #4285f4;
}

.total__users {
    width: 43px;
    height: 43px;
    border-radius: 43px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #61dafb;
    font-size: 15px;
    font-weight: 700;
}